"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  MagnifyingGlassIcon,
  ArrowPathIcon,
  Bars3BottomLeftIcon,
  Squares2X2Icon,
  ViewColumnsIcon,
  ClockIcon,
  InformationCircleIcon,
} from "@heroicons/react/24/outline";

/* =======================
   Types
======================= */

type RiskLevel = "HIGH" | "MEDIUM" | "LOW";
type ViewMode = "grid" | "list" | "detail";

interface ComparisonItem {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: string | null;
}

interface DocumentCard {
  id: string;
  name: string;
  risk: RiskLevel;
  latestVersion: string;
  lastUpdated: string;
  totalComparisons: number;
  fileType: string;
  fileSize?: string;
}

/* =======================
   Config
======================= */

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() ||
  "http://127.0.0.1:8000";

const riskRank: Record<RiskLevel, number> = {
  HIGH: 3,
  MEDIUM: 2,
  LOW: 1,
};

function normalizeRisk(r?: string | null): RiskLevel {
  const x = (r || "").toUpperCase();
  if (x.includes("HIGH")) return "HIGH";
  if (x.includes("MEDIUM")) return "MEDIUM";
  return "LOW";
}

function formatDate(iso: string) {
  try {
    const date = new Date(iso);
    const now = new Date();
    const diffMs = now.getTime() - date.getTime();
    const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));
    
    if (diffDays === 0) return "‡∏ß‡∏±‡∏ô‡∏ô‡∏µ‡πâ";
    if (diffDays === 1) return "‡πÄ‡∏°‡∏∑‡πà‡∏≠‡∏ß‡∏≤‡∏ô";
    if (diffDays < 7) return `${diffDays} ‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà‡πÅ‡∏•‡πâ‡∏ß`;
    
    return new Date(iso).toLocaleDateString("th-TH", {
      day: "numeric",
      month: "short",
      year: "numeric",
    });
  } catch {
    return iso;
  }
}

function getFileType(name: string): string {
  const ext = name.split('.').pop()?.toLowerCase() || '';
  
  if (ext.includes('pdf')) return 'PDF';
  if (['doc', 'docx'].includes(ext)) return 'Word';
  if (['xls', 'xlsx'].includes(ext)) return 'Excel';
  if (['ppt', 'pptx'].includes(ext)) return 'PowerPoint';
  if (['jpg', 'jpeg', 'png', 'gif', 'bmp', 'svg', 'webp'].includes(ext)) return 'Image';
  if (['txt', 'md', 'rtf', 'csv'].includes(ext)) return 'Text';
  if (['zip', 'rar', '7z', 'tar', 'gz'].includes(ext)) return 'Archive';
  if (['mp3', 'wav', 'flac', 'aac', 'ogg'].includes(ext)) return 'Audio';
  if (['mp4', 'avi', 'mov', 'wmv', 'mkv', 'flv', 'webm'].includes(ext)) return 'Video';
  
  return 'Document';
}

/* =======================
   File Style Helper
======================= */

function getFileStyle(type: string) {
  switch (type) {
    case "PDF":
      return {
        bg: "bg-red-600",
        text: "text-red-600",
        border: "border-red-100",
        label: "PDF",
      };
    case "Word":
      return {
        bg: "bg-blue-600",
        text: "text-blue-600",
        border: "border-blue-100",
        label: "DOC",
      };
    case "Excel":
      return {
        bg: "bg-green-600",
        text: "text-green-600",
        border: "border-green-100",
        label: "XLS",
      };
    case "PowerPoint":
      return {
        bg: "bg-orange-500",
        text: "text-orange-500",
        border: "border-orange-100",
        label: "PPT",
      };
    case "Image":
      return {
        bg: "bg-purple-600",
        text: "text-purple-600",
        border: "border-purple-100",
        label: "IMG",
      };
    case "Text":
      return {
        bg: "bg-gray-600",
        text: "text-gray-600",
        border: "border-gray-100",
        label: "TXT",
      };
    case "Archive":
      return {
        bg: "bg-yellow-600",
        text: "text-yellow-600",
        border: "border-yellow-100",
        label: "ZIP",
      };
    case "Audio":
      return {
        bg: "bg-pink-600",
        text: "text-pink-600",
        border: "border-pink-100",
        label: "AUD",
      };
    case "Video":
      return {
        bg: "bg-indigo-600",
        text: "text-indigo-600",
        border: "border-indigo-100",
        label: "VID",
      };
    default:
      return {
        bg: "bg-gray-500",
        text: "text-gray-500",
        border: "border-gray-100",
        label: "DOC",
      };
  }
}

/* =======================
   File Icons with Paper Fold Design
======================= */

function getFileIcon(type: string, size: "sm" | "md" | "lg" = "md") {
  const style = getFileStyle(type);

  const sizeMap = {
    sm: {
      container: "h-8 w-7",
      text: "text-[10px]",
      header: "h-1.5",
      fold: "w-2 h-2",
    },
    md: {
      container: "h-10 w-8",
      text: "text-xs",
      header: "h-2",
      fold: "w-3 h-3",
    },
    lg: {
      container: "h-14 w-12",
      text: "text-sm",
      header: "h-3",
      fold: "w-4 h-4",
    },
  };

  const currentSize = sizeMap[size];

  return (
    <div className={`relative ${currentSize.container} flex-shrink-0`}>
      {/* Paper Background */}
      <div className="absolute inset-0 bg-white rounded-md border border-gray-300 shadow-sm" />

      {/* Folded Corner Effect */}
      <div className="absolute top-0 right-0">
        <div className={`w-0 h-0 border-l-[${currentSize.fold}] border-l-transparent border-b-[${currentSize.fold}] ${style.border} rounded-tr-md`} />
      </div>

      {/* Header Color Bar */}
      <div className={`absolute top-0 left-0 right-0 ${currentSize.header} ${style.bg} rounded-t-md`} />

      {/* Document Label */}
      <div className="absolute inset-0 flex items-center justify-center">
        <span className={`${style.text} ${currentSize.text} font-bold tracking-wide`}>
          {style.label}
        </span>
      </div>
    </div>
  );
}

function generateFileSize() {
  const sizes = ['KB', 'MB'];
  const size = (Math.random() * 5000 + 100).toFixed(0);
  const unit = sizes[Math.floor(Math.random() * sizes.length)];
  return `${size} ${unit}`;
}

/* =======================
   Page
======================= */

export default function DocumentsPage() {
  const [comparisons, setComparisons] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");
  const [viewMode, setViewMode] = useState<ViewMode>("detail");
  const [sortBy, setSortBy] = useState("date");

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    setLoading(true);
    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=300`);
      const data = await res.json();
      setComparisons(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error("Failed to fetch data:", error);
    } finally {
      setLoading(false);
    }
  };

  /* =======================
     Build documents
  ======================= */
  const documents = useMemo<DocumentCard[]>(() => {
    const map = new Map<string, DocumentCard>();

    comparisons.forEach((c) => {
      const name = (c.document_name || "Untitled").trim();
      const fileType = getFileType(name);

      if (!map.has(name)) {
        map.set(name, {
          id: encodeURIComponent(name),
          name,
          risk: normalizeRisk(c.overall_risk_level),
          latestVersion: c.version_new_label || "1.0",
          lastUpdated: c.created_at,
          totalComparisons: 0,
          fileType,
          fileSize: generateFileSize(),
        });
      }

      const doc = map.get(name)!;
      doc.totalComparisons++;

      if (new Date(c.created_at) > new Date(doc.lastUpdated)) {
        doc.lastUpdated = c.created_at;
        doc.latestVersion = c.version_new_label || doc.latestVersion;
      }

      const r = normalizeRisk(c.overall_risk_level);
      if (riskRank[r] > riskRank[doc.risk]) doc.risk = r;
    });

    return Array.from(map.values());
  }, [comparisons]);

  const sortedDocuments = useMemo(() => {
    const docs = [...documents];
    switch(sortBy) {
      case "name":
        return docs.sort((a, b) => a.name.localeCompare(b.name));
      case "date":
        return docs.sort((a, b) => new Date(b.lastUpdated).getTime() - new Date(a.lastUpdated).getTime());
      case "type":
        return docs.sort((a, b) => a.fileType.localeCompare(b.fileType));
      case "risk":
        return docs.sort((a, b) => riskRank[b.risk] - riskRank[a.risk]);
      default:
        return docs;
    }
  }, [documents, sortBy]);

  const filtered = sortedDocuments.filter((d) =>
    d.name.toLowerCase().includes(search.toLowerCase())
  );

  /* =======================
     Statistics
  ======================= */
  const stats = useMemo(() => {
    const total = documents.length;
    const lowRisk = documents.filter(d => d.risk === "LOW").length;
    const mediumRisk = documents.filter(d => d.risk === "MEDIUM").length;
    const highRisk = documents.filter(d => d.risk === "HIGH").length;
    
    return { total, lowRisk, mediumRisk, highRisk };
  }, [documents]);

  /* =======================
     Handle row click for Detail View
  ======================= */
  const handleRowClick = (docName: string) => {
    window.location.href = `/history?document_name=${encodeURIComponent(docName)}`;
  };

  const handleCompareClick = (e: React.MouseEvent, docName: string) => {
    e.stopPropagation();
    window.location.href = `/compare?document_name=${encodeURIComponent(docName)}`;
  };

  /* =======================
     Get icon size based on view mode
  ======================= */
  const getIconSizeForView = () => {
    switch(viewMode) {
      case "grid": return "md";
      case "list": return "sm";
      case "detail": return "sm";
      default: return "md";
    }
  };

  /* =======================
     UI
  ======================= */

  return (
    <div className="min-h-screen bg-gray-50 pb-16">
      {/* Header */}
      <div className="bg-white border-b shadow-sm">
        <div className="max-w-full mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-xl font-bold text-gray-900">üìÅ Document Management System</h1>
              <p className="text-gray-600 text-sm mt-1">
                Total: {stats.total} documents ‚Ä¢ Safe: {stats.lowRisk} ‚Ä¢ High Risk: {stats.highRisk}
              </p>
            </div>
            <div className="flex items-center gap-4">
              <div className="relative">
                <MagnifyingGlassIcon className="h-5 w-5 text-gray-400 absolute left-3 top-1/2 -translate-y-1/2" />
                <input
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                  placeholder="Search documents..."
                  className="pl-10 pr-4 py-2 text-sm border border-gray-300 rounded-lg bg-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500 w-64"
                />
              </div>
              <button
                onClick={fetchData}
                className="p-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
                title="Refresh data"
              >
                <ArrowPathIcon className={`h-5 w-5 text-gray-600 ${loading ? 'animate-spin' : ''}`} />
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Toolbar */}
      <div className="bg-white border-b px-4 py-3">
        <div className="max-w-full mx-auto flex items-center justify-between">
          <div className="flex items-center gap-4">
            <div className="flex items-center border border-gray-300 rounded-lg overflow-hidden">
              <button
                onClick={() => setViewMode("grid")}
                className={`p-2 ${viewMode === "grid" ? "bg-blue-50 text-blue-600 border-r border-gray-300" : "hover:bg-gray-50"}`}
                title="Grid view"
              >
                <Squares2X2Icon className="h-4 w-4" />
              </button>
              <button
                onClick={() => setViewMode("list")}
                className={`p-2 ${viewMode === "list" ? "bg-blue-50 text-blue-600 border-r border-gray-300" : "hover:bg-gray-50"}`}
                title="List view"
              >
                <Bars3BottomLeftIcon className="h-4 w-4" />
              </button>
              <button
                onClick={() => setViewMode("detail")}
                className={`p-2 ${viewMode === "detail" ? "bg-blue-50 text-blue-600" : "hover:bg-gray-50"}`}
                title="Detail view"
              >
                <ViewColumnsIcon className="h-4 w-4" />
              </button>
            </div>
            
            <select 
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="px-3 py-2 text-sm border border-gray-300 rounded-lg bg-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            >
              <option value="date">Sort by Date</option>
              <option value="name">Sort by Name</option>
              <option value="type">Sort by Type</option>
              <option value="risk">Sort by Risk</option>
            </select>
          </div>
          
          <div className="text-sm text-gray-600">
            Showing {filtered.length} of {documents.length} items
          </div>
        </div>
      </div>

      {/* Content Area */}
      <div className="p-4">
        {/* Loading State */}
        {loading && (
          <div className="flex flex-col items-center justify-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500 mb-4"></div>
            <span className="text-gray-600">Loading documents...</span>
          </div>
        )}

        {/* Empty State */}
        {!loading && filtered.length === 0 && (
          <div className="flex flex-col items-center justify-center h-64 bg-white rounded-lg border border-gray-200">
            <div className="relative h-14 w-12 mb-4">
              <div className="absolute inset-0 bg-white rounded-md border border-gray-300 shadow-sm" />
              <div className="absolute top-0 right-0">
                <div className="w-0 h-0 border-l-[16px] border-l-transparent border-b-[16px] border-gray-100 rounded-tr-md" />
              </div>
              <div className="absolute top-0 left-0 right-0 h-3 bg-gray-400 rounded-t-md" />
              <div className="absolute inset-0 flex items-center justify-center">
                <span className="text-gray-400 text-sm font-bold tracking-wide">DOC</span>
              </div>
            </div>
            <h3 className="text-lg font-medium text-gray-900">No documents found</h3>
            <p className="mt-1 text-gray-500">
              {search ? "Try searching with different keywords" : "No documents in the system yet"}
            </p>
            <button
              onClick={fetchData}
              className="mt-4 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Refresh Data
            </button>
          </div>
        )}

        {/* Grid View */}
        {!loading && viewMode === "grid" && (
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-6 gap-4">
            {filtered.map((doc) => (
              <Link
                key={doc.id}
                href={`/history?document_name=${encodeURIComponent(doc.name)}`}
                className="group bg-white border border-gray-200 rounded-lg p-4 hover:shadow-md transition-all hover:border-blue-300"
              >
                <div className="flex flex-col items-center text-center">
                  <div className="mb-3 relative">
                    {getFileIcon(doc.fileType, "md")}
                    <div className={`absolute -bottom-1 -right-1 h-3 w-3 rounded-full border-2 border-white ${
                      doc.risk === "HIGH" ? "bg-red-500" :
                      doc.risk === "MEDIUM" ? "bg-amber-500" :
                      "bg-green-500"
                    }`} />
                  </div>
                  <div className="w-full">
                    <div className="font-medium text-sm truncate group-hover:text-blue-600" title={doc.name}>
                      {doc.name}
                    </div>
                    <div className="text-xs text-gray-500 mt-2 space-y-1">
                      <div className="font-semibold">{doc.fileType}</div>
                      <div>Version {doc.latestVersion}</div>
                      <div>{formatDate(doc.lastUpdated)}</div>
                      <div className="flex items-center justify-center gap-2 mt-2">
                        <span className={`px-2 py-0.5 rounded text-[10px] font-medium ${
                          doc.risk === "HIGH" ? "bg-red-50 text-red-700" :
                          doc.risk === "MEDIUM" ? "bg-amber-50 text-amber-700" :
                          "bg-green-50 text-green-700"
                        }`}>
                          {doc.risk}
                        </span>
                        <span className="text-gray-400">‚Ä¢</span>
                        <span>{doc.totalComparisons} comp</span>
                      </div>
                    </div>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        )}

        {/* List View */}
        {!loading && viewMode === "list" && (
          <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
            {filtered.map((doc) => (
              <Link
                key={doc.id}
                href={`/history?document_name=${encodeURIComponent(doc.name)}`}
                className="flex items-center p-4 border-b border-gray-100 last:border-b-0 hover:bg-gray-50 group transition-colors"
              >
                <div className="mr-4">
                  {getFileIcon(doc.fileType, "sm")}
                </div>
                <div className="flex-1 min-w-0">
                  <div className="flex items-center justify-between">
                    <div className="font-medium truncate group-hover:text-blue-600">
                      {doc.name}
                    </div>
                    <div className="text-sm text-gray-500 ml-4">
                      {doc.fileSize}
                    </div>
                  </div>
                  <div className="flex items-center gap-4 mt-1 text-sm text-gray-500">
                    <span className="font-semibold">{doc.fileType}</span>
                    <span>‚Ä¢</span>
                    <span>Version {doc.latestVersion}</span>
                    <span>‚Ä¢</span>
                    <span>{doc.totalComparisons} comparisons</span>
                    <span>‚Ä¢</span>
                    <span>{formatDate(doc.lastUpdated)}</span>
                  </div>
                </div>
                <div className="ml-4">
                  <span className={`px-3 py-1 rounded text-xs font-medium ${
                    doc.risk === "HIGH" ? "bg-red-50 text-red-700" :
                    doc.risk === "MEDIUM" ? "bg-amber-50 text-amber-700" :
                    "bg-green-50 text-green-700"
                  }`}>
                    {doc.risk}
                  </span>
                </div>
              </Link>
            ))}
          </div>
        )}

        {/* Detail View */}
        {!loading && viewMode === "detail" && (
          <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
            {/* Table Header */}
            <div className="grid grid-cols-12 gap-4 px-6 py-3 text-sm font-medium text-gray-600 border-b border-gray-100 bg-gray-50">
              <div className="col-span-5">Name</div>
              <div className="col-span-2">Last Modified</div>
              <div className="col-span-2">Type</div>
              <div className="col-span-1">Size</div>
              <div className="col-span-2">Risk Level</div>
            </div>

            {/* Table Rows */}
            {filtered.map((doc) => (
              <div
                key={doc.id}
                onClick={() => handleRowClick(doc.name)}
                className="grid grid-cols-12 gap-4 px-6 py-4 border-b border-gray-100 last:border-b-0 hover:bg-blue-50 group cursor-pointer transition-colors"
              >
                <div className="col-span-5 flex items-center">
                  <div className="mr-3">
                    {getFileIcon(doc.fileType, "sm")}
                  </div>
                  <div className="min-w-0">
                    <div className="font-medium truncate group-hover:text-blue-600">
                      {doc.name}
                    </div>
                    <div className="text-sm text-gray-500">
                      Version {doc.latestVersion} ‚Ä¢ {doc.totalComparisons} comparisons
                    </div>
                  </div>
                </div>
                <div className="col-span-2 flex items-center text-sm text-gray-600">
                  {formatDate(doc.lastUpdated)}
                </div>
                <div className="col-span-2 flex items-center">
                  <span className="text-sm font-medium text-gray-700">{doc.fileType}</span>
                </div>
                <div className="col-span-1 flex items-center text-sm text-gray-600">
                  {doc.fileSize}
                </div>
                <div className="col-span-2 flex items-center">
                  <div className="flex items-center gap-3">
                    <span className={`px-3 py-1 rounded text-xs font-medium ${
                      doc.risk === "HIGH" ? "bg-red-50 text-red-700" :
                      doc.risk === "MEDIUM" ? "bg-amber-50 text-amber-700" :
                      "bg-green-50 text-green-700"
                    }`}>
                      {doc.risk}
                    </span>
                    <div className="flex gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                      <button
                        onClick={(e) => handleCompareClick(e, doc.name)}
                        className="p-1 hover:bg-gray-200 rounded transition-colors"
                        title="Compare"
                      >
                        <InformationCircleIcon className="h-4 w-4 text-gray-600" />
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Status Bar */}
      {!loading && (
        <div className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 px-4 py-2 text-sm text-gray-600">
          <div className="flex justify-between items-center">
            <div>
              {filtered.length} items displayed
            </div>
            <div className="flex items-center gap-6">
              <div className="flex items-center gap-2">
                <div className="h-3 w-3 rounded-full bg-green-500"></div>
                <span>Safe: {stats.lowRisk}</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="h-3 w-3 rounded-full bg-amber-500"></div>
                <span>Medium Risk: {stats.mediumRisk}</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="h-3 w-3 rounded-full bg-red-500"></div>
                <span>High Risk: {stats.highRisk}</span>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}